import 'semantic-ui-css/semantic.min.css'
import React from "react";
import ReactDom from "react-dom";
import App from "./App";
import { createUploadLink } from "apollo-upload-client";
import { ApolloClient } from "apollo-client";
import { InMemoryCache } from "apollo-cache-inmemory";
import { ApolloProvider } from "@apollo/react-hooks";
import { SubscriptionClient } from 'subscriptions-transport-ws';
import { split } from 'apollo-link';
import { getMainDefinition } from 'apollo-utilities';

const url = true ? "https://laudos.herokuapp.com/graphql" : "http://localhost:3000/graphql";
const ws = true ? "wss://laudos.herokuapp.com/subscriptions" : "ws://localhost:3000/subscriptions";
const httpClient = createUploadLink({ uri: url, credentials: 'include' });
const wsLink = new SubscriptionClient(ws, {
    reconnect: true
});

const link = split(
    ({ query }) => {
        const { kind, operation } = getMainDefinition(query);
        return kind == 'OperationDefinition' && operation == 'subscription';
    }, wsLink, httpClient
);

const client = new ApolloClient({
    //credentials: 'include',
    link,
    cache: new InMemoryCache()
});

var Client = () => (
    <ApolloProvider client={client}>
        <App />
    </ApolloProvider>
)

ReactDom.render(<Client />, document.getElementById("root"));