import React from "react";
import ListarExamesContainer from "../../Graphql/containers/Exame/listarExames";
import { Modal, Header, Menu } from "semantic-ui-react";
import FormCadastrarExame from "../Forms/CadastrarExame";
import './examesList.css'
import { EXAME_SALVO_SUBSCRIPTION } from "../../Graphql/containers/Subscriptions/Exame/exameSalvo";

class Registries extends React.Component {
    constructor(props) {
        super(props);
        this.buttonClickHandler = this.buttonClickHandler.bind(this);
        this.modalCloseHandler = this.modalCloseHandler.bind(this);
        this.modoExibicao = this.modoExibicao.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.filtrar = this.filtrar.bind(this);
        this.submitHandle = this.submitHandle.bind(this);
        this.state = {
            isOpen: false,
            exibicaoTabela: true,
            filter: ''
        };

        this.props.subscribeToMore({
            document: EXAME_SALVO_SUBSCRIPTION,
            variables: { id: 1 },//props.match.params.ID
            updateQuery: (prev, { subscriptionData }) => {
                if (!subscriptionData.data)
                    return prev;
                const newlaudo = subscriptionData.data.laudoSalvo;
                if (!prev.listarExames.find((msg) => msg.id == newlaudo.id)) {
                    return Object.assign({}, prev, {
                        listarExames: Object.assign([], prev.listarExames, [...prev.listarExames, newlaudo])
                    });
                } else
                    return prev;
            }
        });
    }

    buttonClickHandler() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }
    modalCloseHandler() {
        this.setState({
            isOpen: false
        });
    }

    modoExibicao(isTabela) {
        this.setState({
            exibicaoTabela: isTabela
        });
    }
    handleInputChange(evt) {
        var target = evt.target;
        var value = target.type === 'checkbox' ? target.checked : target.value;
        var name = target.name;
        this.setState({
            [name]: value
        });
    }

    filtrar(elemento) {
        var result = elemento.protocolo.match(this.state.filter) || elemento.nome.match(this.state.filter);
        return result;
    }

    submitHandle(result) {
        this.setState({
            isOpen: false
        });
    }

    render() {
        if (this.props.loading)
            return <div>Loading...</div>
        else if (this.props.error)
            return <div>Erro...</div>

        this.resultList = this.props.data.listarExames
        if (this.state.filter)
            this.resultList = this.resultList.filter(this.filtrar);
        var tabela;
        if (this.state.exibicaoTabela) {
            tabela = (
                <div className='ui doubling four stackable tiny cards'>
                    {this.resultList.map(exame => (
                        <div key={exame.id} className='ui yellow card'>
                            <div className='content'>
                                <div className='header'>{exame.protocolo} - {exame.nome}</div>
                                <div className='meta'>
                                    <div className='ui ribbon label'>{exame.dataExame ? exame.dataExame : (new Date().toISOString().substr(0, 10) + ' ' + new Date().toISOString().substr(11, 5))}</div>
                                </div>
                                <div className='description bottom'>
                                    <a href={(true ? 'https://laudos.herokuapp.com/download?id=' : 'http://localhost:3000/download?id=') + exame.id + '&download=exame'}>Baixar Laudo</a>
                                </div>
                            </div>
                            <div className='extra content'>
                                <button className='ui primary small button btn-default'>Remover</button>
                                <button className='ui small button btn-default'>Remover</button>
                            </div>
                        </div>
                    ))}
                </div>)
        } else {
            tabela = (
                <div role='list' className='ui animated huge divided relaxed list'>
                    {this.resultList.map(exame => (
                        <div key={exame.id} role='listitem' className='item'>
                            <i aria-hidden='true' className='chart bar icon large middle aligned'></i>
                            <div className='content'>
                                <a className='header'>{exame.protocolo} - {exame.nome}</a>
                                <a className='description'>Teste</a>
                                <button className='ui small button btn-default'>Remover</button>
                            </div>
                        </div>
                    ))}
                </div>
            )
        }

        return (
            <div>
                <div className='ui menu'>
                    <a className="item" onClick={this.buttonClickHandler}>
                        <i aria-hidden="true" className="add icon"></i>
                        Novo Exame
                    </a>
                    <div className='right menu'>
                        <Menu.Item active={!this.state.exibicaoTabela} onClick={() => this.modoExibicao(false)}>
                            <i aria-hidden='true' className='icon list' />
                        </Menu.Item>
                        <Menu.Item active={this.state.exibicaoTabela} onClick={() => this.modoExibicao(true)}>
                            <i aria-hidden='true' className='icon th' />
                        </Menu.Item>
                        <div className='item'>
                            <div className='ui icon input'>
                                <input type='text' placeholder='Filtro' name='filter' value={this.state.filter} onChange={this.handleInputChange} />
                                <i aria-hidden='true' className='search icon' />
                            </div>
                        </div>
                    </div>
                </div>
                <div className='ui segment'>
                    <div>Total de Registros: {this.props.data.listarExames.length}</div>
                    <div className='tabelaContainer'>
                        {tabela}
                    </div>
                </div>
                <Modal onClose={this.modalCloseHandler} open={this.state.isOpen}>
                    <Header icon='upload' content="Cadastrar novo exame" />
                    <Modal.Content>
                        <FormCadastrarExame submitHandle={this.submitHandle} />
                    </Modal.Content>
                </Modal>
            </div >
        )
    }
}

export default () => (<ListarExamesContainer>{({ ...props }) => <Registries {...props} />}</ListarExamesContainer>)
